import Boom from '@hapi/boom';
import UsersModel from '../models/user';

export default async function getUserByMobileNumber(mobileNumber) {
  const Users = UsersModel();
  const userInDB = await Users.findOne({ mobileNumber: `+91${mobileNumber}` });
  if (!userInDB) throw Boom.notFound('No account found for this mobile number');
  return userInDB;
}
